@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@200;300;400;500;600;700&display=swap);
:root{
  --primary: #a10101;
  --secondary: black;
  --error: #a10101;
}

body{
  font-family: "Heebo";
  color: black;
  color: var(--secondary);
}
.App{
  max-width: 960px;
  margin: 0 auto;
}
#logo {
  display: flex;
  align-items: center;
}
.title h1{
  color: #a10101;
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
  margin-left: 20px;
}
.title h2, .title p{
  text-align: center;
}
.title h2{
  margin-top: 60px;
  font-size: 2.6rem;
}
.title img {
  height: 80px;
}

/* upload form styles */
form{
  margin: 30px auto 10px;
  text-align: center;
}
label input{
  height: 0;
  width: 0;
  opacity: 0;
}
label{
  display: block;
  width: 100px;
  border: 1px solid #a10101;
  border: 1px solid var(--primary);
  border-radius: 10px;
  margin: 10px auto;
  line-height: 30px;
  color: #a10101;
  color: var(--primary);
  font-weight: 500;
  font-size: 17px;
  padding: 5px 15px;
}
label:hover{
  background: #a10101;
  background: var(--primary);
  color: white;
}
.output{
  height: 60px;
  font-size: 0.8rem;
}
.error{
  color: #a10101;
  color: var(--error);
}

/* progress bar styles */
.progress-bar{
  height: 5px;
  background: #a10101;
  background: var(--primary);
  margin-top: 20px;
}

/* image grid styles */
.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  position: relative;
}
.img-wrap img{ 
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.5s;
}
.img-wrap :hover{
  margin-top: -10px;
}
.img-wrap img :hover{
  margin-top: -10px;
}
/* modal styles */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 3px solid white;
}
